// ** React Imports
import { Fragment, useState, useEffect, forwardRef } from 'react'
import { Link } from 'react-router-dom'
// ** Invoice List Sidebar
// ** Table Columns
import { columns } from './columns'

// ** Store & Actions
import { getData } from '../store'
import { useDispatch, useSelector } from 'react-redux'
import axios from '@src/configs/axios/axiosConfig'
// ** Third Party Components
import Select from 'react-select'
import ReactPaginate from 'react-paginate'
import DataTable from 'react-data-table-component'
import { ChevronDown, Share, Grid } from 'react-feather'

// ** Utils
import { selectThemeColors } from '@utils'

// ** Reactstrap Imports
import {
  Row,
  Col,
  Card,
  Input,
  Label,
  Button,
  CardBody,
  DropdownMenu,
  DropdownItem,
  DropdownToggle,
  UncontrolledDropdown
} from 'reactstrap'

// ** Styles
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'

// ** Table Header
const CustomHeader = ({ handlePerPage, rowsPerPage, handleFilter, searchTerm }) => {
  // ** Converts table to CSV


  return (
    <div className='invoice-list-table-header w-100 me-1 ms-50 mt-2 mb-75'>
      <Row>
        <Col xl='6' className='d-flex align-items-center p-0'>
          <div className='d-flex align-items-center w-100'>
            <label htmlFor='rows-per-page'>Show</label>
            <Input
              className='mx-50'
              type='select'
              id='rows-per-page'
              value={rowsPerPage}
              onChange={handlePerPage}
              style={{ width: '5rem' }}
            >
              <option value='10'>10</option>
              <option value='25'>25</option>
              <option value='50'>50</option>
            </Input>
            <label htmlFor='rows-per-page'>Entries</label>
          </div>
        </Col>
        <Col
          xl='6'
          className='d-flex align-items-sm-center justify-content-xl-end justify-content-start flex-xl-nowrap flex-wrap flex-sm-row flex-column pe-xl-1 p-0 mt-xl-0 mt-1'
        >
          <div className='d-flex align-items-center mb-sm-0 mb-1 me-1'>
            <label className='mb-0' htmlFor='search-invoice'>
              Search:
            </label>
            <Input
              id='search-invoice'
              className='ms-50 w-100'
              type='text'
              value={searchTerm}
              onChange={e => handleFilter(e.target.value)}
            />
          </div>

          <div className='d-flex align-items-center table-header-actions'>
            <UncontrolledDropdown className='me-1'>
              <DropdownToggle color='secondary' caret outline>
                <Share className='font-small-4 me-50' />
                <span className='align-middle'>Export</span>
              </DropdownToggle>
              <DropdownMenu>
                <DropdownItem className='w-100'>
                  <Grid className='font-small-4 me-50' />
                  <span className='align-middle'>Excel</span>
                </DropdownItem>
              </DropdownMenu>
            </UncontrolledDropdown>

            <Button className='add-new-user' color='primary' tag={Link} to='/task/add'>
              Add Task
            </Button>
          </div>
        </Col>
      </Row>
    </div>
  )
}

const TaskList = () => {
  // ** Store Vars
  const dispatch = useDispatch()
  const store = useSelector(state => state.task)

  // ** States
  const [sort, setSort] = useState('desc')
  const [searchTerm, setSearchTerm] = useState('')
  const [currentPage, setCurrentPage] = useState(1)
  const [sortColumn, setSortColumn] = useState('id')
  const [rowsPerPage, setRowsPerPage] = useState(10)
  const [currentClient, setCurrentClient] = useState({ id: '', name: 'Select Client' })
  const [currentService, setCurrentService] = useState({ id: '', name: 'Select Service' })
  const [currentStatus, setCurrentStatus] = useState({ value: '1', label: 'To Do', number: 0 })
  const [currentUser, setCurrentUser] = useState({ id: '', name: 'Select Employee' })

  // ** Bootstrap Checkbox Component
  const BootstrapCheckbox = forwardRef((props, ref) => (
    <div className='form-check'>
      <Input type='checkbox' ref={ref} {...props} />
    </div>
  ))

  // ** Get data on mount
  useEffect(() => {
    dispatch(
      getData({
        sort,
        sortColumn,
        q: searchTerm,
        page: currentPage,
        perPage: rowsPerPage,
        user: currentUser.id,
        client: currentClient.id,
        recurringFlag: false,
        status: currentStatus.value,
        service: currentService.id
      })
    )
  }, [dispatch, store.data.length, sort, sortColumn, currentPage])

  // ** User filter options
  const statusOptions = [
    { value: '1', label: 'To Do' },
    { value: '2', label: 'In Progress' },
    { value: '3', label: 'Completed' },
    { value: '4', label: 'On Hold' },
    { value: '5', label: 'Cancelled' },
    { value: '6', label: 'Sent to Review' },
    { value: '7', label: 'Request Changes' }
  ]

  const [clientOptions, setClientOptions] = useState([])
  const [serviceOptions, setServiceOptions] = useState([])
  const [userOptions, setuserOptions] = useState([])

  const getClients = () => {
    axios.post('/clients/dropdown').then(response => {
      const arr = response.data
      setClientOptions(arr.clients)
    })
  }

  const getServices = () => {
    axios.post('/services/dropdown').then(response => {
      const arr = response.data
      setServiceOptions(arr.services)
    })
  }

  const getOrganizationUsers = async () => {
    axios.post('/organizationusers/dropdown').then(response => {
      const arr = response.data
      setuserOptions(arr.organizationusers)
    }).catch((err) => {
      console.log(err)
    })

  }

  useEffect(() => {
    getOrganizationUsers()
    getServices()
    getClients()
  }, [])


  // ** Function in get data on page change
  const handlePagination = page => {
    dispatch(
      getData({
        sort,
        sortColumn,
        q: searchTerm,
        perPage: rowsPerPage,
        page: page.selected + 1,
        client: currentClient.id,
        status: currentStatus.value,
        service: currentService.id,
        recurringFlag: false
      })
    )
    setCurrentPage(page.selected + 1)
  }

  // ** Function in get data on rows per page
  const handlePerPage = e => {
    const value = parseInt(e.currentTarget.value)
    dispatch(
      getData({
        sort,
        sortColumn,
        q: searchTerm,
        perPage: value,
        page: currentPage,
        client: currentClient.id,
        service: currentService.id,
        user: currentUser.id,
        recurringFlag: false,
        status: currentStatus.value
      })
    )
    setRowsPerPage(value)
  }

  // ** Function in get data on search query change
  const handleFilter = val => {
    setSearchTerm(val)
    dispatch(
      getData({
        sort,
        q: val,
        sortColumn,
        page: currentPage,
        perPage: rowsPerPage,
        client: currentClient.id,
        status: currentStatus.value,
        service: currentService.id,
        recurringFlag: false
      })
    )
  }

  // ** Custom Pagination
  const CustomPagination = () => {
    const count = Number(Math.ceil(store.total / rowsPerPage))

    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        pageCount={count || 1}
        activeClassName='active'
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={page => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        containerClassName={'pagination react-paginate justify-content-end my-2 pe-1'}
      />
    )
  }

  // ** Table data to render
  const dataToRender = () => {
    const filters = {
      client: currentClient.id,
      service: currentService.id,
      user: currentUser.id,
      status: currentStatus.value,
      recurringFlag: false,
      q: searchTerm
    }

    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })

    if (store.data.length > 0) {
      return store.data
    } else if (store.data.length === 0 && isFiltered) {
      return []
    }
  }

  const handleSort = (column, sortDirection) => {
    setSort(sortDirection)
    setSortColumn(column.sortField)
    dispatch(
      getData({
        sort,
        sortColumn,
        q: searchTerm,
        page: currentPage,
        perPage: rowsPerPage,
        client: currentClient.id,
        status: currentStatus.value,
        user: currentUser.id,
        recurringFlag: false,
        service: currentService.id
      })
    )
  }

  return (
    <Fragment>
      <Card>
        <CardBody>
          <Row>
            <Col md='3'>
              <Label for='role-select'>Client</Label>
              <Select
                label={'name'}
                isClearable={false}
                value={currentClient}
                options={clientOptions}
                className='react-select'
                classNamePrefix='select'
                theme={selectThemeColors}
                getOptionLabel={(option) => option.name}
                getOptionValue={(option) => option.id}
                onChange={data => {
                  setCurrentClient(data)
                  dispatch(
                    getData({
                      sort,
                      sortColumn,
                      q: searchTerm,
                      client: data.id,
                      page: currentPage,
                      perPage: rowsPerPage,
                      status: currentStatus.value,
                      user: currentUser.id,
                      recurringFlag: false,
                      service: currentService.id
                    })
                  )
                }}
              />
            </Col>
            <Col className='my-md-0 my-1' md='3'>
              <Label for='plan-select'>Service</Label>
              <Select
                theme={selectThemeColors}
                isClearable={false}
                className='react-select'
                classNamePrefix='select'
                options={serviceOptions}
                value={currentService}
                getOptionLabel={(option) => option.name}
                getOptionValue={(option) => option.id}
                onChange={data => {
                  setCurrentService(data)
                  dispatch(
                    getData({
                      sort,
                      sortColumn,
                      q: searchTerm,
                      page: currentPage,
                      perPage: rowsPerPage,
                      client: currentClient.id,
                      user: currentUser.id,
                      service: data.id,
                      recurringFlag: false,
                      status: currentStatus.value
                    })
                  )
                }}
              />
            </Col>
            <Col md='3'>
              <Label for='role-select'>Emloyee</Label>
              <Select
                isClearable={false}
                value={currentUser}
                options={userOptions}
                className='react-select'
                classNamePrefix='select'
                theme={selectThemeColors}
                getOptionLabel={(option) => option.name}
                getOptionValue={(option) => option.id}
                onChange={data => {
                  setCurrentUser(data)
                  dispatch(
                    getData({
                      sort,
                      sortColumn,
                      q: searchTerm,
                      client: currentClient.id,
                      page: currentPage,
                      perPage: rowsPerPage,
                      user: data.id,
                      status: currentStatus.value,
                      recurringFlag: false,
                      service: currentService.id
                    })
                  )
                }}
              />
            </Col>
            <Col md='3'>
              <Label for='status-select'>Status</Label>
              <Select
                theme={selectThemeColors}
                isClearable={false}
                className='react-select'
                classNamePrefix='select'
                options={statusOptions}
                value={currentStatus}
                onChange={data => {
                  setCurrentStatus(data)
                  dispatch(
                    getData({
                      sort,
                      sortColumn,
                      q: searchTerm,
                      page: currentPage,
                      status: data.value,
                      perPage: rowsPerPage,
                      client: currentClient.id,
                      service: currentService.id
                    })
                  )
                }}
              />
            </Col>
          </Row>
        </CardBody>
      </Card>

      <Card className='overflow-hidden'>
        <div className='react-dataTable'>
          <DataTable
            noHeader
            subHeader
            sortServer
            pagination
            responsive
            selectableRows
            paginationServer
            columns={columns}
            onSort={handleSort}
            sortIcon={<ChevronDown />}
            className='react-dataTable'
            paginationComponent={CustomPagination}
            data={dataToRender()}
            selectableRowsComponent={BootstrapCheckbox}
            subHeaderComponent={
              <CustomHeader
                store={store}
                searchTerm={searchTerm}
                rowsPerPage={rowsPerPage}
                handleFilter={handleFilter}
                handlePerPage={handlePerPage}
              />
            }
          />
        </div>
      </Card>


    </Fragment>
  )
}

export default TaskList
